body {
  color: 383b43;
  background-color: #ffffff;
  font-family: 'Inter', sans-serif;
  letter-spacing: 0.5px;
}

.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  flex-direction: column;
}

.App > img {
  width: 250px;
  height: 200px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-Body {
  width: 90%;
  height: 90%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.first > a:link {
  color: white;
  text-decoration: none;
}

.first > a:visited {
  color: white;
}

.Filter-Menu {
  display: flex;
  flex-direction: row;
  gap: 1.5em;
  justify-content: center;
  align-items: center;
  margin-top: 2em;
  margin-bottom: 3em;
  list-style-type: none;
}

.Filter-Menu > li {
  list-style-type: none;
}

.Menu-Button {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Menu-Button > label {
  margin-top: 0.5em;
  font-size: 0.7em;
  font-weight: 500;
}

.Menu-Icons {
  display: flex;
  font-size: 1.5em;
  background: #383b43;
  width: 2.5em;
  height: 2.5em;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
}

.Filter-Menu a:link {
  color:white;
  text-decoration: none;
}
.Filter-Menu a:visited {
  color:white;
}

.first {
  display: flex;
  flex-direction: row;
  gap: 1em;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-bottom: 2em;
}

.searchBar {
  margin: 2em 2em;
}

.categoryIcon {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  width: 150px;
  height: 225px;
  border-radius: 8px;
  justify-content: flex-end;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-start;
  animation-delay: 0.1s;
  animation-name: fadeInRight;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

@keyframes fadeInRight {
  from {
      opacity: 0;
      transform: translateX(30%);
  }
  to { opacity: 1 }
}

.categoryIcon > h1 {
  text-align: left;
  margin-left: 4vw;
  margin-right: 4vw;
  margin-bottom: 2em;
  letter-spacing: 0.5px;
  font-size: 1em;
  font-weight: 500;
  color: #f3f3ff;
  text-shadow: 1px black;
  -webkit-text-stroke: 0.1px black;
  max-width: 25vw;
  overflow: hidden;
}

.categoryIcon > img {
  width: 150px;
  height: 225px;
  border-radius: 8px;
}

.ingredients {
  display: flex;
  flex-direction: row;
  gap: 1em;
}

.Drink-List-Header {
  font-size: 1.2em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2em;
  margin-bottom: 0;
  color: #383b43;
}

.Drink-List {
  width: 100vw;
}

.Drink-List > a:link {
  color: white;
  text-decoration: none;
}

.Drink-List > a:visited {
  color: white;
}

.Drink-List-Item {
  width: 100%;
  animation-delay: 0.1s;
  animation-name: fadeInRight;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

.Drink-List-DrinkData {
  display: flex;
  flex-direction: row;
  width: 80vw;
  margin: auto;
  gap: 1.5em;
  font-size: 1.4em;
  margin-bottom: 1em;
}

.Drink-List-DrinkData > img{
  width: 100px;
  height: 100px;
  border-radius: 16px 16px 16px 0;
}

 .Drink-Category {
  margin-top: 0.4em;
  color: #b2b2b2;
  font-size: 0.5em;
  font-weight: 500;
  margin-bottom: 0;
}

.Drink-Category-Loading {
  margin-top: 0.4em;
  width: 4em;
  margin-bottom: 0;
  height: 0.5em;
  background-color: #B2B2B2;
}

.Drink-Text {
  color: #383b43;
  margin-bottom: 0;
  margin-top: 1em;
  font-size: 0.8em;
  font-weight: 700;
}

.Drink-Text-Loading {
  background-color: #B2B2B2;
  margin-bottom: 0;
  margin-top: 1em;
  width: 6em;
  height: 0.5em;
}

.Drink-Card {
  display: flex;
  flex-direction: column;
  height: 90vh;
  width: 100vw;
  margin: auto;
}

.Drink-Card > img {
  height: 50vh;
  width: 100vw;
  margin-bottom: 1.5em;
  border-radius: 0 0 0 4em;
}

.Back-Arrow {
  color: white;
  display: flex;
  position: absolute;
  font-size: 2em;
  width: 1.3em;
  height: 1.3em;
  border-radius: 50%;
  border: none;
  align-items: center;
  justify-content: center;
  margin-top: 5vh;
  opacity: 80%;
  margin-left: 5vw;
  background-color: #383b43;
  transition: 0.6s ease-out;
}

.Back-Arrow:hover {
  transition: 0.6s ease-in;
  opacity: 100;
}

.Drink-Card > nav > a:link {
  color: white;
}

.Drink-Card > nav > a:visited {
  color: white;
}

@keyframes fadeInBottom {
  from {
      opacity: 0;
      transform: translateY(60%);
  }
  to { opacity: 1 }
}

.Drink-Card > .Drink-Category {
  margin-top: 1em;
  margin-left: 10vw;
  margin-right: 10vw;
  color: #9b9da1;
  font-size: 1em;
  font-weight: 500;
  margin-bottom: 0;
  animation-delay: 0.2s;
  animation-name: fadeInBottom;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

.Drink-Card > .Drink-Name {
  color: #383b43;
  margin-left: 10vw;
  margin-right: 10vw;
  font-size: 1.8em;
  font-weight: 800;
  margin-top: 0;
  animation-delay: 0.3s;
  animation-name: fadeInBottom;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

.Drink-Card > .Drink-Label {
  color: #383b43;
  margin-left: 10vw;
  margin-right: 10vw;
  letter-spacing: 1px;
  font-size: 1em;
  font-weight: 800;
  margin-bottom: 0;
  animation-delay: 0.4s;
  animation-name: fadeInBottom;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

.Drink-Card > .Drink-Ingredients {
  margin-left: 10vw;
  margin-right: 10vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-basis: 27%;
  gap: 1em;
}

.Drink-Card > .Drink-Ingredients-Text {
  margin-top: 1.5em;
  color: #383b43;
  margin-left: 10vw;
  margin-right: 10vw;
}

.Drink-Card > .Drink-Ingredients-Text > .Drink-Measure {
  margin: 0;
  color: #b2b2b2;
  font-size: 0.7em;
  font-weight: 600;
  animation-delay: 0.5s;
  animation-name: fadeInBottom;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

.Drink-Card > .Drink-Ingredients-Text > .Drink-Ingredient {
  margin-top: 0.3em;
  margin-bottom: 1.5em;
  font-size: 1.2em;
  font-weight: 600;
  animation-delay: 0.6s;
  animation-name: fadeInBottom;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

.Drink-Card > .Drink-Instructions-Text {
  margin-left: 10vw;
  margin-right: 10vw;
  font-size: 1em;
  font-weight: 500;
  padding-bottom: 4em;
  animation-delay: 0.7s;
  animation-name: fadeInBottom;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

.SearchBar {
  margin-top: 2em;
}

.SearchBar > form > input {
  color: #383b43;
  width: 60vw;
  font-size: 1em;
  font-weight: 600;
  height: 1.8em;
  border: 2px solid #383b43;
  border-radius: 8px;
  padding: 6px 2em;
}

.Search-Button {
  display: inline-block;
  position: absolute;
  top: 5.8em;
  right: 5vw;
  background-color: #383b43;
  border: none;
  color: white;
  padding: 0.9em;
  text-align: center;
  text-decoration: none;
  border-radius: 8px;
  font-size: 1em;
}